export default {
  components: {},
  mounted() {
    this.setDarkMode();
  },
  data() {
    return {
      darkMode: false,
      colorMode: "dark"
      //colorMode: ["system", "light", "dark"]
    };
  },
  methods: {
    setDarkMode() {
      this.darkMode = true;
      this.colorMode = "dark";
      this.$colorMode.preference = this.colorMode;
    },
    setLightMode() {
      this.darkMode = false;
      this.colorMode = "light";
      this.$colorMode.preference = this.colorMode;
    }
  }
};
